import React from 'react';

export default function MadeHint () {
	return (
		<div>
			<style jsx>{`
				div {
					color: rgba(0,0,0,.25);
					text-align: center;
				}

				div > span {
					font: 10px;
				}

				@media screen {
					div {
						margin: -2em auto 2em;
						text-align: center;
					}
				}

				@media print {
					div {
						margin: 0 auto;
					}

					div > span {
						font: 8pt;
						opacity: .5;
					}
				}
			`}</style>
			<span>Created using Node + React + Next.js</span>
		</div>
	);
};